import React from "react";

// component
import { Container, Row, Col, Image } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { isMobile } from "react-device-detect";

export default function PageMinistryDetails() {
  const location = useLocation();
  const data = location.state;

  return (
    <>
      <Container fluid className="ministry-container m-0 p-0">
        <Row className="m-0 px-lg-5 w-100 h-auto justify-content-center">
          <Col
            lg={12}
            className={`${
              isMobile ? "w-100" : "w-75"
            } d-flex justify-content-center justify-content-lg-start`}
          >
            <h1 className="ministry-details-header">{data.content_title}</h1>
          </Col>
          <Col
            lg={12}
            className={`${
              isMobile ? "w-100" : "w-75"
            } d-flex justify-content-center justify-content-lg-start ministry-details-col`}
            dangerouslySetInnerHTML={{
              __html: data.content_description.sub_content_section,
            }}
          ></Col>
        </Row>
      </Container>
      <Container fluid className="m-0 p-0 mt-lg-3 w-100 h-auto">
        <Row lg={6} className="m-0 px-lg-5 w-100 h-100 justify-content-center">
          <Col lg={12} className={`${isMobile ? "w-100" : "w-75"} m-0 p-0`}>
            <Row className="m-0 p-0 h-100">
              <Col lg={6} className={`${isMobile ? "order-2" : ""}`}>
                <Row>
                  <Col lg={12}>
                    <small
                      className="ministry-details-small"
                      dangerouslySetInnerHTML={{
                        __html: data.content_description.content_description,
                      }}
                    ></small>
                  </Col>
                  <Col lg={12}>
                    <h1 className="ministry-get-involved pt-3">GET INVOLVED</h1>
                  </Col>
                  <Col lg={12}>
                    <small
                      className="ministry-details-small"
                      dangerouslySetInnerHTML={{
                        __html: data.content_description.content_invitation,
                      }}
                    ></small>
                  </Col>
                </Row>
              </Col>
              <Col lg={6} className={`${isMobile ? "order-1" : ""}`}>
                <Row>
                  <Col lg={12}>
                    <Image
                      className="ministry-image-list py-3"
                      src={
                        process.env.REACT_APP_API_URL +
                        "/images/contents/" +
                        data.content_thumbnail
                      }
                    />
                  </Col>
                </Row>
              </Col>
              <Col lg={12} className={`${isMobile ? "order-3" : ""} mt-lg-5`}>
                <Row>
                  <Col lg={12}>
                    <small
                      className="ministry-details-small"
                      dangerouslySetInnerHTML={{
                        __html: data.content_description.content_footer,
                      }}
                    ></small>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
      <Container fluid className="m-0 p-0 h-15"></Container>
    </>
  );
}
